import Fuse from 'fuse.js'
import {graphql, useStaticQuery} from 'gatsby'
import path from 'path'
import React from 'react'

function useSearch(query) {
  const data = useStaticQuery(graphql`
    {
      allMdx {
        nodes {
          id
          frontmatter {
            title
          }
          rawBody
        }
      }
      allSitePage {
        nodes {
          path
          context {
            mdxId
          }
        }
      }
    }
  `)

  const mdxNodes = data.allMdx.nodes.reduce((map, obj) => {
    map[obj.id] = obj;
    return map;
  }, { });

  const list = React.useMemo(
    () =>
      data.allSitePage.nodes.filter(node => {
        return (node.context && node.context.mdxId && mdxNodes[node.context.mdxId] != null);
      }).map(node => {
        const mdxNode = mdxNodes[node.context.mdxId];

        const obj =
        {
          path: node.path,
          title: mdxNode.frontmatter.title,
          rawBody: mdxNode.rawBody
        };
        return obj;
      }),
    [data],
  )

  const fuse = React.useMemo(
    () =>
      new Fuse(list, {
        threshold: 0.2,
        keys: ['title', 'rawBody'],
        tokenize: true,
      }),
    [list],
  )

  const [results, setResults] = React.useState(list)

  React.useEffect(() => {
    if (query) {
      setResults(fuse.search(query).slice(0, 20)) // Return top 20 results
    } else {
      setResults(list)
    }
  }, [query, fuse, list])

  return results
}

export default useSearch
